@import url(https://fonts.googleapis.com/css?family=Roboto|Suez+One);
* {
  margin: 0;
  padding: 0;
  outline: none;
}

html, body, #root {
  width: 100%;
  height: 100%;
}


body {
  background-color: #fafafa;
  font-family: Trebuchet MS, Helvetica;
}
#photo-info-alert {
  top: 5px;
  background-color: #808080c7;
  color: white;
  position: fixed;
}

.gallery .gallery-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
}

.gallery .gallery-item {
  position: relative;
  margin: 2px;
  width: 150px;
  overflow: hidden;
}

.gallery .gallery-item .note {
  font-size: 0.87em;
}

.gallery .gallery-item .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 30px;
  width: 100%;
  opacity: 0;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  background-color: #80808075;
}

.gallery .gallery-item:hover .overlay {
  opacity: 0.6;
}

.gallery .gallery-item .overlay .info-icon {
  height: 25px;
  width: 25px;
  margin-top: 3px;
  float: right;
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.controls {
  display: -webkit-flex;
  display: flex;
}

.controls .filter {
  width:40%
}

.controls .search-input {
  width: 60%;
}
main {
  height: calc(100% - 82px);
  background-color: #fffdd0;
  padding: 82px 1.5% 0;
}
